import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import styled from 'styled-components'
import { Col, Container, Row } from 'react-bootstrap';


const PStyle = styled.p`
    font-size: 20px;
    color: #FF8906;
    @media (max-width: 524px){
        font-size: 18px;
    }
`
const Testy = styled.p`
    font-size: 20px;
    color: white;
    @media (max-width: 524px){
    font-size: 18px;
    }
`


const TPersons = () => {
    return (
        <div>
            <Container>
                <Row className="justify-content-md-center pt-3">
                    <Col sm={4}>
                        <PStyle><img src="https://i.imgur.com/ViKFVUF.png" alt="" /> Juan Antonio</PStyle>
                        <Testy>Tengo algunos años trabajando con Javascript y aún así aprendí varias cosas importantes de Jhosep y como utiliza Javascript en el día a día.</Testy>
                    </Col>
                    <Col sm={4}>
                        <PStyle><img src="https://i.imgur.com/bWvgFYC.png" alt="" /> Elena Caceres</PStyle>
                        <Testy>Jhosep ha sido de gran ayuda para acelerar ciertos esfuerzos de desarrollo. Lo que nos hubiera llevado 6 meses solo les llevó 1,5 meses.</Testy>
                    </Col>
                    <Col sm={4}>
                        <PStyle><img src="https://i.imgur.com/jwTyGmC.png" alt="" /> Albert Flores</PStyle>
                        <Testy>La forma como explica y trabaja los temas Jhosep, me ha ayudado a comprender y sacarle el mayor provecho a HTML y CSS. </Testy>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default TPersons