import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Col, Container, Row } from 'react-bootstrap'
import styled from 'styled-components'

const DivRelative = styled.div`
    background: gray;
    width: 580px;
    height: 300px;
    margin-top: 100px;
    bottom: 170px;
    left: 15px;
    position: relative;
    @media (max-width: 524px){
        width: 90%;
        height: 100%;
        margin-top: 50%;
        bottom: 170px;
        left: 15px;
    }
`

const DivAbsolute = styled.div`
    background: #0F0E17;
    width: 380px;
    height: 220px;
    top: 55%;
    right: 38%;
    position: absolute;
    @media (max-width: 524px){
        width: 90%;
        height: 100%;
        top: 55%;
        right: 8%;
    }
`
const DivAbsoluteTwo = styled.div`
    background: #0F0E17;
    width: 380px;
    height: 220px;
    top: 55%;
    right: 38%;
    position: absolute;
    @media (max-width: 524px){
        width: 113%;
        height: 170%;
        top: 55%;
        right: 0;
    }
`

const ImgStyle = styled.img`
    background: gray;
    width: 580px;
    height: 450px;
    bottom: 170px;
    left: 0;
    @media (max-width: 524px){
        width: 100%;
        height: auto;
        bottom: 0;
        left: 0;
    }
`
const H1Title = styled.h1`
    font-size: 25px;
    color: white;
    margin-left: 30px;
    margin-top: 30px;
    @media (max-width: 524px){
        font-size: 18px;
    }
`
const PStyle = styled.p`
    font-size: 19px;
    color: #FF8906;
    margin-left: 30px;
    margin-top: 15px;
    @media (max-width: 524px){
        font-size: 18px;
    }
`


function PMain() {
    return (
        <div className="projects" id="projects">
            <Container fluid className="">
                <Row className="align-items-center">
                    <Col sm={6} className="">
                        <DivRelative>
                            <ImgStyle src="https://i.imgur.com/Fp9PMMj.png" />
                            <DivAbsolute>
                                <H1Title>AppDoption</H1Title>
                                <PStyle>La Aplicacion perfecta para encontrar el hogar para las adorables masccotas.</PStyle>
                                <a className="btn btn-warning ms-4" href="https://j-roma.github.io/Sprint1-Adopcion/">Ver proyecto Completo</a>

                            </DivAbsolute>
                        </DivRelative>
                    </Col>
                    <Col sm={6} className="p-5">
                        <DivRelative>
                            <ImgStyle src="https://i.imgur.com/fhFDLny.png" />
                            <DivAbsoluteTwo>
                                <H1Title>Principe Fresco</H1Title>
                                <PStyle>App de fantasia, sobre una linea de ropa del fomoso principe del Rap.</PStyle>
                                <a className="btn btn-warning ms-4" href="https://luischdu.github.io/EcomerceWorkshop/">Ver proyecto Completo</a>

                            </DivAbsoluteTwo>
                        </DivRelative>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default PMain;