import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row} from 'react-bootstrap';

import styled from 'styled-components'
import CFApp from './CForm';

const DivStyle = styled.div`
    background: #FEB692;
`
const BlackBox = styled.section`
    background: #0F0E17;
    margin-left: 25%;
    padding-top: 50px;
    padding-bottom: 100px;
    width: 700px;
    @media (max-width: 524px){
        margin-left: 0;
        padding-top: 0;
        padding-botton: 0;
        width: auto;
    }
`

const H1Title = styled.h1`
    font-size: 55px;
    color: white;
    @media (max-width: 524px){
        font-size: 50px;
    }
`
const PStyle = styled.p`
    font-size: 20px;
    color: #FF8906;
    @media (max-width: 524px){
        font-size: 18px;
    }
`

function Contact() {
    return (
        <DivStyle>
            <BlackBox>
            <Container className="pt-5" fluid="sm" bg="dark" id="contact">
                <Row md={2} className="justify-content-center text-center">
                    <H1Title>Contacto</H1Title>
                </Row>
                <Row md={2} className="pt-3 justify-content-center">
                    <PStyle>Si esta interesado en mi perfil, no dude en contactarme</PStyle>
                </Row>
                <CFApp/>
            </Container>
            </BlackBox>
        </DivStyle>
    );
}

export default Contact;