import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Navbar, Nav, Form, Button, Container } from 'react-bootstrap'
import styled from 'styled-components'

const DivStyle = styled.div`
    background: #0F0E17; 
`

function Header() {
  return (
    <DivStyle>
      <div className="navigationBar">
        <Container fluid>
          <Navbar collapseOnSelect expand="lg" variant="dark" id="navbar">
            <Navbar.Brand href="#home" className="ms-5">ROMA</Navbar.Brand>
            <Navbar.Toggle aria-controls="navbar" />
            <Navbar.Collapse id="navbar">
              <Nav className="mr-auto">
                <Nav.Link href="#features" className="ms-2">Hola</Nav.Link>
                <Nav.Link href="#pricing" className="ms-2">Proyectos</Nav.Link>
                <Nav.Link href="#pricing" className="ms-2">Testimonios</Nav.Link>
                <Nav.Link href="#pricing" className="ms-2">Contacto</Nav.Link>
              </Nav>
              <Form inline className="d-flex" >
                <Button variant="warning" className="ms-2">Descargar Curriculum</Button>
              </Form>
            </Navbar.Collapse>
          </Navbar>
        </Container>
      </div>
    </DivStyle>
  );
}

export default Header;
